import React from 'react'
import './header.css'
import CTA from './CTA'
import ProfilePic from '../../assets/bojan_tanasic_profile.png'
import HeaderSocials from './HeaderSocials'
import '../../fonts/font.css'


const Header = () => {
  return (
    <header>
      <div className="container header__container">
        <h5>Hello I'm</h5>
        <h1 className='title-name'>Bojan Tanasic</h1>
        <h5 className="text-light">Junior Web Developer</h5>
        <CTA />
        <HeaderSocials />
        <div className="me">
          <img src={ProfilePic} alt="bojan_profile" />
        </div>

        <a href="#contact" className='scroll__down'>Scroll Down</a>


      </div>

    </header>

  )
}

export default Header