import React from 'react'
import './services.css'
import {BsCheckLg} from 'react-icons/bs'

const Services = () => {
  return (
    <section id='services'>
      <h5>What I offer</h5>
      <h2>Services</h2>
      <div className="container services__container">
        <article className="service">
          <div className="service__head">
            <h3>UI/UX Graphics Design</h3>
          </div>

          <ul className='service__list'>
            <li><BsCheckLg className='service__list-icon' />
              <p>Illustrating design ideas.</p>
            </li>
            <li><BsCheckLg className='service__list-icon' />
              <p>Building navigation components.</p>
            </li>
            <li><BsCheckLg className='service__list-icon' />
              <p>Designing graphic user interface elements, <br/>
                 building navigation buttons.</p>
            </li>
            <li><BsCheckLg className='service__list-icon' />
              <p>Developing UI mockups, <br/>creating original graphic designs.</p>
            </li>
            
          </ul>  
        </article>
          
          {/* END OF UI GRAPHICS DESIGN DIVISION */}
          <article className="service">
          <div className="service__head">
            <h3>Web Development</h3>
          </div>

          <ul className='service__list'>
            <li><BsCheckLg className='icon1' />
              <p>Designing and implementing user interface components for 
                JavaScript-based web and mobile applications using the React open-source 
                library ecosystem.</p>
            </li>
            <li><BsCheckLg className='icon2' />
              <p>Meeting with the development team to discuss user interface ideas and applications.</p>
            </li>
            <li><BsCheckLg className='icon3' />
              <p>Developing and implementing front-end architecture to support user interface concepts.
                </p>
            </li>
            <li><BsCheckLg className='icon4' />
              <p>Documenting application changes and developing updates. </p>
            </li>
            
          </ul>  
        </article>
      </div>
    </section>
  )
}

export default Services