import React from 'react'
import './about.css'
import ME from '../../assets/bojan_tanasic_about.jpg'
import {MdDesignServices} from 'react-icons/md'
import {BsFillCameraFill} from 'react-icons/bs'
import {BsFillFileEarmarkCodeFill} from 'react-icons/bs'

const about = () => {
  return (
    <section id='about'>
      <h5>Get to Know</h5>
      <h2>About Me</h2>

      <div className="container about__container">
        <div className="about__me">
          <div className="about__me-image">
            <img src={ME} alt="About Image" />
          </div>
        </div>

        
        <div className="about__content">
          <div className="about__cards">
            <article className='about__card'>
            <BsFillFileEarmarkCodeFill className='about__icon'/>
            <h5>Web Development</h5>
            <small> 1+ Years</small>
            </article>
            <article className='about__card'>
            <MdDesignServices className='about__icon'/>
            <h5>Graphic Design</h5>
            <small> 5+ Years</small>
            </article>
            <article className='about__card'>
            <BsFillCameraFill className='about__icon'/>
            <h5>Photography</h5>
            <small> 10+ Years</small>
            </article>
          </div>
          <p>
          I'm Bojan Tanasic a passionate graphics designer and <br />
          web developer from Novi Sad, Serbia.
          </p>
          <p>
          I enjoy using my skills to contribute to the exciting technological advances that happen everyday.
          I graduated from the Novi Sad, Medical High School in 2007.
          During my high school years I've shown exceptional interest in graphic design
          and photo editing which eventually led me to frontend web development.
          </p>

          
          <a href="#contact" className='btn btn-primary'>Get In Touch</a>
        </div>


      </div>

      

      


      
    </section>
  )
}

export default about